.gallery {
  position: relative;
  display: grid;
  grid-template-columns: repeat(5, 230px);
  height: 241px;
  padding: 0;
  list-style-type: none;
}

.gallery__wrapper {
  position: relative;
  width: 230px;
  height: 241px;
  padding-bottom: 241px;
}

