  background-repeat: no-repeat;
  background-position: center;
  content: "";
  opacity: 0;
  transition: opacity 0.2s ease-out;
  pointer-events: none;
}

.gallery__wrapper:hover::before {
  opacity: 1;
}

.gallery__link {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.gallery__link:hover {
  opacity: 1;
}

.gallery__link img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  pointer-events: none;
}

picture {
  pointer-events: none;
}

.gallery__spare {
  padding: 12px;
  font-family: var(--Nunito);
  font-size: 30px;
  font-weight: 700;
  color: var(--light-green);
  border: 2px solid var(--light-green);
  border-radius: 8px;
}
